.header {
  background-color: #0ff;
  padding: 40px 0;
}

img {
  max-width: 100%;
  margin: 0 auto;
  display: block;
}

.box {
  width: 900px;
  background-color: #efe30e;
  margin: 0 auto;
  padding: 100px;
}

.box:not(:last-child) {
  margin-bottom: 300px;
}

html, body {
  height: 100%;
  position: relative;
}

body {
  color: #000;
  background: #eee;
  margin: 0;
  padding: 0;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
}

.swiper {
  width: 300px;
  height: 300px;
  margin-top: -150px;
  margin-left: -150px;
  position: absolute;
  top: 50%;
  left: 50%;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
}

.swiper-slide img {
  width: 100%;
  display: block;
}

.swiper-pagination-bullet-active {
  background-color: #dc143c;
}

.overlay {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  z-index: 20;
  background-color: rgba(0, 0, 0, .5);
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  max-width: 500px;
  z-index: 30;
  text-align: center;
  background-color: #fff;
  border-radius: 3px;
  padding: 30px;
  transition: all .3s;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 3px 10px -.5px rgba(0, 0, 0, .2);
}

.modal.active, .overlay.active {
  opacity: 1;
  visibility: visible;
}

.modal__cross {
  width: 15px;
  height: 15px;
  fill: #444;
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
}

a {
  color: #fff;
  background-color: #414b52;
  border-radius: 3px;
  margin: 10px;
  padding: 20px;
  text-decoration: none;
  display: inline-block;
}

